function determineIconType (type) {
  switch (type) {
    case 'twitter':
      return <div className="twitter">
        <img src={require('../images/twitter.svg')} />
      </div>;
    case 'linkedin':
        return <div className="linkedin">
          <img src={require('../images/linkedin.svg')} />
        </div>;
    default: 
      return <div className="twitter">
         <img src={require('../images/twitter.svg')} />
      </div>
  }
}

const ProfileCard = ({ name, avatar, description, links, active }) => {
  return (
    <div className={`profile-card ${active ? 'active' : ''}`}>
      <div className="avatar">
        {avatar()}
      </div>
      <div className='words'>
        <div className="title">
          <div className="name">{name} <span>|</span></div>
          {links.map((link, i) => <a key={i} href={link.url} target="_blank" className="social">
            {determineIconType(link.type)}
          </a>)}
        </div>
        <div className='desc'>{description}</div>
      </div>
    </div>
  );  
}

const Team = () => {
  const [active, setActive] = React.useState(0);

  const clickHandler = (i) => e => {
    e.preventDefault();
    setActive(i);
  }
  return (
    <Block classes="team" header="The Team">
      <div className="profile-container">
        {profiles.map((profile, i) => <ProfileCard {...profile} key={i} active={i === active}/>)}
      </div>
      <div className="slider-controls">
        {profiles.map((profile, i) => <span onClick={clickHandler(i)} key={i} className={`${active === i ? 'active' : ''}`}></span>)}
      </div>
    </Block>
  );
}

export default Team;