import React from 'react';
import Block from './Block';
import './economics.scss';

const features = [
  {
    icon: <div className="eth">
      <img src={require('../images/econs/big-eth.svg')} />
    </div>, 
    stat: {
      value: 15, 
      denom: '%',
    }, 
    desc: 'There is a daily lottery where players compete for 15% of all revenues across Pic Lab games.'
  }, 
  {
    icon: <div className="burn">
      <img src={require('../images/econs/burn.svg')} />
    </div>, 
    stat: {
      value: 35, 
      denom: '%',
    }, 
    desc: 'Each month, Pic Labs will use 35% of its revenues to buy back and burn PIC.'
  }, 
];

const Feature = ({ icon, stat, desc }) => {
  return (
    <div className="feature-card">
      <div className="icon">{icon}</div>
      <div className="divider"></div>
      <div className='set'>
        <div className="desc typo typo-regular copy">{desc}</div>
        <div className="stat">{stat.value}{stat.denom}</div>
      </div>
    </div>
  );
}

const Economics = () => {
  return (
    <Block 
      classes="economics" 
      header="Token Economics" 
      copy="Players can earn PIC tokens by enjoying all the games made by PicLabs.">
      <div className="feat-container">
        {features.map((feat, i) => <Feature key={i} {...feat}/>)}
      </div>
    </Block>
  );
}

export default Economics;